@-webkit-keyframes imageslider{
    0%{
        left: 0;
    }
    25%{
        left: -380px;
    }
    50%{
        left: -760px;
    }
    75%{
        left: -1140px;
    }
	100%{
		left: -1500px;
	}
    
}

@keyframes imageslider{
    0%{
        left: 0;
    }
    25%{
        left: -380px;
    }
    50%{
        left: -760px;
    }
    75%{
        left: -1140px;
    }
	100%{
		left: -1500px;
	}
    
}

.slider{
    width: 250%;
    -webkit-animation: imageslider 25s infinite;
            animation: imageslider 25s infinite;
    position: relative;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

