  #wrapper{
    font-family: sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    height: 100vh;
    margin-bottom: 50px;
    margin-top: 50px;
    /* background-color: #ffa000; */
  }

  .payment{
    display: -webkit-flex;
    display: flex;
    width: 90%;
    height: 90%;
    background-color: #fff;
    box-shadow: 0px 0px 30px 15px rgba(0,0,0,0.2);
  }

  .payment_product{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    width: 40%;
    height: 100%;
    background-size: cover;
    color: rgba(0, 66, 128,0.8);
    padding: 40px;
  }

  .payment_bill{
    padding: 40px;
    width: 80%;
    height: 100%;
  }

  .ticketdetail{
    display: -webkit-flex;
    display: flex;
    margin-top: 20px;
  }

  .ticketdetail h2{
    color: #ffa000;
  }
  .price{
    background-color: #ffa000;
    color: white;
    display: inline-block;
    padding: 10px 30px;
    font-size: 22px;
    font-weight: 500;
    border-radius: 2px;
    margin-left: 20px;
  }

  #wrapper h3{
    margin-top: 10px;
    color: rgba(0, 66, 128,0.8);
    font-weight: 400;
    font-size: 30px;
    margin-bottom: 20px;
  }

  #wrapper ul{
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  #wrapper li{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    line-height: 2.5;
    color: #666;
  }

  #wrapper li:nth-last-child(1){
    color: rgba(0, 66, 128,0.8);
  }

  #wrapper hr{
    border-color: #ebeaea;
    border-style: dashed;
  }

  .payment_form input,select{
    border: none;
    background-color: #e9ecec;
    padding: 15px 10px;
    width: 32%;
    border-radius: 2px;
  }

  .payment_form .form_input{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 25px;
  }

  .payment_form button{
    width: 100%;
    border: none;
    background-color: rgba(0, 66, 128,0.8);
    color: #fff;
    padding: 15px 0;
    font-size: 20px;
    border-radius: 2px;
    cursor: pointer;
    transition: 0.5s;
  }

  .payment_form button:hover{
    background-color: #0d6efd;
  }
